.main{
	height:100vh;
	width:100vw;
	text-align: center;
}
.inner{
	margin:10px;
	padding:100px;
	position:relative;
	width:500px;
	left:20%;
}
.inner>input{
	margin:30px;
}
button{
	background:black;
	color:white;
	margin:10px;
	padding:20px;
	border:none;
	font-family:Reem kufi;
	font-size:21px;
	border-radius:20px;
}
input{
	border-radius:20%;
	border:none;
	height:50px;
	width:50px;
	background:black;
}
button:hover{
	cursor:pointer;
}
input:focus{
	outline:none;
}
h1{
	color:black;
	font-size:30px;
	font-weight:bolder;
	margin:10px;
	padding:20px;
}